.buttons{width:100%;margin-top:8px;display:flex;justify-content:space-between;align-items:center}.buttons__add{width:100%;height:40px;background-color:#313237;color:#fff;cursor:pointer;transition:box-shadow .3s ease-in-out}.buttons__add:hover{box-shadow:inset 0 0 16px #858484}.buttons__add_dark{background-color:#905bff}.buttons__add_dark:hover{box-shadow:inset 0 0 16px #2d2c2c}.buttons__add_active{background-color:#fff;border:1px solid #89939a;color:#27ae60}.buttons__add_dark_active{background-color:#323542;color:#fff;border:none}.buttons__like{width:50px;height:40px;margin-left:8px;background-image:url(../../../../public/img/headerIсons/heart.svg);background-size:16px 16px;background-repeat:no-repeat;background-position:center;border:1px solid #89939a;cursor:pointer;transition:all .3s ease-in-out}.buttons__like_active{background-image:url(../../../../public/img/headerIсons/activeHert.svg)}.buttons__like:hover{border:1px solid #313237}.buttons__like_dark{background-image:url(../../../../public/img/headerIсons/heart-dark.svg);background-color:#323542;border:none}.buttons__like_dark:hover{background-color:#4a4d58;border:none}.buttons__like_dark_active{background-image:url(../../../../public/img/headerIсons/activeHert.svg)}